<style>

</style>

<template>
  <div>
    <h1>node.js+express+mysql+webpack+vue+axios</h1>
    <h2>axios</h2>
    <h3>webpackwebpageswebstatics</h3>
    <pre v-if="getData!=null">
        {{getData}}
    </pre>
  </div>
</template>

<script>
export default {
    data () {
        return {
            getData:null
        }

    },
    created(){
        var _this=this;
        this.$axios({
            method: 'get',
            url: '/testApi'
         }).then(function(result){
            _this.getData=result.data;
            console.log(JSON.stringify(result))
         });
    }
}
</script>



